@tailwind base;
@tailwind components;
@tailwind utilities;

.dark {
  @apply bg-zinc-900 text-zinc-50;
}

.light {
  @apply bg-stone-200 text-black;
}

input {
  @apply m-1 p-1 dark:bg-zinc-800 border-2 rounded;
}

h1 {
  @apply font-bold text-xl mb-2 uppercase;
}

h2 {
  @apply font-bold text-lg mb-3;
}
